* {
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serifs;
  font-weight: 100s;
}
#root {
  margin: 2em;
}
body {
  min-height: 100vh;
  max-width: 100vw;
  background: linear-gradient(180deg, #b8febb 0%, #b9e8f6 100%);
}

.block {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 70vh;
  width: 100%;
  max-width: 480px;
  animation-name: appear;
  animation-duration: 1s;
}

.stats-block {
  justify-content: space-between;
}

.standard-container {

  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 70%;
  margin: 1em;
  padding: 5%;
  background: rgba(255, 255, 255, 0.24);
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 12px rgba(17, 19, 68, 0.25);
  border-radius: 24px;
}

.stats-container {
  min-height: 40%;
  justify-content: space-evenly;
}
.flex-space-between {
  display: flex;
  justify-content: space-between;
  padding: 0.5em;
}

h1 {
  font-size: large;
  color: #fff;

}

.form-input-title {
color: #000;
font-size: medium;
}
.new-project-description {
  font-size: 0.1em;

}
.new-project-button{
  font-size: small;

}
.new-project-button.new {
  font-size: medium;
}
.new-project-input{
  font-size: small;
}
@media screen and (width > 700px) {
  .form-input-title {
    font-size: large;
    }
  h1 {
    font-size: x-large;
  }
  .new-project-button{
    font-size: medium;
  }
  .new-project-input{
    font-size: medium;
  }

}
@media screen and (width > 1200px) {
  .form-input-title {
    font-size: x-large;
    }
  .new-project-input{
    font-size: large;
  }
  .new-project-button{
    font-size: large;
  
  }
  .new-project-button.new {
    font-size: xx-large;
  
  }
  h1 {
    font-size: xx-large;
  }
  body {
    overflow: scroll;
  }
  .statc-block {
    justify-content: space-between;
  }
}
@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

